.wrapperbox {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 2em;
    padding-bottom: 2em;
    overflow: hidden;

    .bigcircle {

        border-radius: 300px;
        height: 80vh;
        width: 80vh;
        border: solid;
        display: flex;
        justify-content: center;
        align-items: center;

        p {
            font-family: 'Quicksand', sans-serif;
            font-weight: 200;
            font-size: 1.4em;
            text-align: center;

            @media screen and (max-width: 600px) {

                padding-left: 3em;
                padding-right: 3em;

            }
        }

        .icons {
            display: flex;
            justify-content: center;
            align-items: center;

            .box {
                padding: 0.8em;
            }
        }

        border-color: #B11025;

        @media screen and (max-width: 600px) {
            border-radius: 0;
            height: 100%;
            width: 100%;
            border: none;
            padding-bottom: 5em;

        }


    }
}